import navBar from '@/components/nav-bar.vue'
import productList from "@/components/product-list.vue"
import uniLoadMore from '@/components/uni-load-more.vue';
import {
	getOtherGood,
} from '@/api/goods.js'
export default {
	data() {
		return {
			type:'',
			screen:'',
			jg:'',
			page:1,
			active: 0,
			isup: 3,
			count: 0,
			recommend: null,
			title:"",
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
		}
	},
	components: {
		navBar,
		productList,
		uniLoadMore
	},
	computed: {
		up() {
			let color = this.isup == 0 ? '#ff0000' : '#ccc'
			return color
		},
		down() {
			let color = this.isup == 1 ? '#ff0000' : '#ccc';
			return color;
		}
	},
	onLoad(option) {
		this.type = option.type || '';
		this.title = option.title || '爱省购';
		this._getGoodsList({type:this.type});
	},
	//到底加载更多
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		let ret = getOtherGood({
			page: this.page,
			type: this.type,
			screen:this.screen,
			jg: this.jg
		});
		ret.then(res => {
			if (res.code == 200) {
				if (res.result.length == 0) {
					this.loadingType = 3;
					return
				}
				this.recommend = this.recommend.concat(res.result);
				this.page++;
				this.loadingType = 0;
			} else {
				this._showError()
			}
		})
	},
	methods: {
		back(){
			uni.switchTab({
				url:'/pages/index/index'
			})
		},
		_showError() {
			uni.showToast({
				title: "出错了...",
				icon: 'none',
				mask: true
			})
		},
		//条件筛选
		screentap(index) {
			this.screen = index;
			if (index == 3) {
				this.active = index;
				this.isup = this.count % 2 == 0 ? this.isup = 0 : this.isup = 1;
				this.count++;
				console.log("this.isip", this.isup)
				this.jg = this.isup;
				this._getGoodsList({screen:index,jg:this.isup});
				return
			}
			this._getGoodsList({screen:index});
			this.active = index
			this.isup = 3;
		},
		_getGoodsList({page = 0, type = '', screen = '', jg = ''}) {
			uni.showLoading({
				title: '加载中..'
			})
			let ret = getOtherGood({
				page,
				type:this.type,
				screen,
				jg
			});
			ret.then(res => {
				uni.hideLoading();
				if (res.code == 200) {
					this.recommend = res.result;
					console.log(this.recommend)
				} else {
					this._showError()
				}
			})
		},
	}
}
